//sighting.js
// --by Steve F
module.exports = {
  err: function err(msg) {
    throw new Error('Error (Code S' + msg + ')');
  },
  isValidDate: function isValidDate(input) {
    var dt = new Date(input);
    return dt instanceof Date && !isNaN(dt);
  },
  isValidSightingDatetime: function isValidSightingDatetime(sightingDatetime) {
    if (sightingDatetime === null) this.err(11.0);
    if (sightingDatetime === '') this.err(11.1);
    if (!this.isValidDate(sightingDatetime)) this.err(11.2);
    return true;
  },
  isValidCarsSighted: function isValidCarsSighted(carsSighted) {
    if (carsSighted === null) this.err(12);
    if (carsSighted === '') this.err(12.1);
    if (!isNaN(carsSighted)) this.err(12.2);
    if (typeof carsSighted == 'object') this.err(12.3);
    return true;
  },
  prepCarsSighted: function prepCarsSighted(carsSighted) {
    return carsSighted.join(',');
  },
  isValidSource: function isValidSource(source) {
    if (source === null) this.err(13);
    if (source === '') this.err(13.1);
    if (!isNaN(source)) this.err(13.2);
    if (typeof source == 'object') this.err(13.3);
    if (source.indexOf(',') > -1) this.err(13.4);
    return true;
  },
  isValidDatabase: function isValidDatabase(database) {
    if (database === null) this.err(14);
    if (typeof database !== 'object') this.err(14.1);
    if (typeof database.save !== 'function') this.err(14.2);
    if (typeof database.getAll !== 'function') this.err(14.2);
    if (typeof database.get !== 'function') this.err(14.2);
    if (typeof database.delete !== 'function') this.err(14.2);
    return true;
  },
  isValidSightingName: function isValidSightingName(sightingName) {
    if (sightingName === null) this.err(15);
    if (sightingName === '') this.err(15.1);
    if (typeof sightingName !== 'string') this.err(15.2);
    return true;
  },
  createSighting: function createSighting(sightingName, sightingDatetime, carsSighted, source, database) {
    var preppedCarsSighted = this.prepCarsSighted(carsSighted);
    this.isValidSightingName(sightingName);
    this.isValidSightingDatetime(sightingDatetime);
    this.isValidCarsSighted(preppedCarsSighted);
    this.isValidSource(source);
    this.isValidDatabase(database);

    try {
      var saveResult = database.save(sightingName, sightingDatetime, carsSighted);
      return saveResult;
    } catch (err) {
      this.err(15);
    }
  }
};