var sighting = require('./src/sighting.js');

var rtsightings = module.exports = function (sightingName, sightingDatetime, carsSighted, source, db) {
  try {
    var result = sighting.createSighting(sightingName, sightingDatetime, carsSighted, source, db);
    var message = '';
  } catch (err) {
    var result = '';
    var message = err.message;
  }

  var data = {
    result: result,
    message: message
  };
  return data;
};